<template>

      <div class="">


    <section class="layout-top">
      <header class="header">
        <div class="header-dock al">
          <router-link to="/demos"><i class="icon iconfont icon-back"></i></router-link>

        </div>
        <div class="header-body">
          {{currentComponent}}
        </div>
      </header>
    </section>
    <section class="layout-has-top">

      <router-view></router-view>


    </section>
    </div>

</template>

<script>

export default {
  name: 'menudemo',
  components: {

  },
  created() {
    this.init()
  },
  methods: {
    init() {
      this.currentComponent = this.$route.path.replace('/demo/', '')
    }
  },
  watch: {
    $route: 'init'
  },
  data() {
    return {
      currentComponent: ''
    }
  }
}
</script>



